import React from 'react';
import { useNavigate } from 'react-router-dom';

const NotFound = (props) => {
  const navigate = useNavigate();

  return (
    <div>
      <h1>  !</h1>
      <button
        onClick={() => {
          navigate("/detail");
        }}>
      </button>
    </div>
  );
};

export default NotFound;